import { defineStore } from 'pinia'
import { ref, computed } from 'vue'
import { usePlantStore } from './plant'
import { taskApi } from '../services/api'
import { ElMessage } from 'element-plus'

export const useTaskStore = defineStore('task', () => {
  // æ‰€æœ‰ä»»åŠ¡æ•°æ®
  const tasks = ref([])
  const systemTasks = ref([])
  const completedTasks = ref([])
  const loading = ref(false)
  
  // è®¡ç®—å±æ€§ï¼šå¾…å®Œæˆä»»åŠ¡
  const pendingTasks = computed(() => tasks.value.filter(task => !task.completed))
  
  // è·å–æ‰€æœ‰ä»»åŠ¡
  const fetchTasks = async () => {
    try {
      loading.value = true
      const response = await taskApi.getTasks()
      tasks.value = response.tasks.filter(task => !task.completed)
      completedTasks.value = response.tasks.filter(task => task.completed)
    } catch (error) {
      ElMessage.error('è·å–ä»»åŠ¡åˆ—è¡¨å¤±è´¥')
    } finally {
      loading.value = false
    }
  }
  
  // è·å–ç³»ç»Ÿä»»åŠ¡
  const fetchSystemTasks = async () => {
    try {
      loading.value = true
      const response = await taskApi.getSystemTasks()
      systemTasks.value = response.tasks
    } catch (error) {
      ElMessage.error('è·å–ç³»ç»Ÿä»»åŠ¡å¤±è´¥')
    } finally {
      loading.value = false
    }
  }
  
  // æ·»åŠ ä»»åŠ¡
  const addTask = async (task) => {
    try {
      const response = await taskApi.createTask({
        title: task.title,
        description: task.description || '',
        deadline: task.deadline || '',
        important: task.important || false
      })
      
      // æ·»åŠ åˆ°æœ¬åœ°çŠ¶æ€
      tasks.value.push(response.task)
      
      // ä¸å†ç»™åˆ›å»ºä»»åŠ¡å¢åŠ ç»éªŒå€¼
      ElMessage.success('ä»»åŠ¡åˆ›å»ºæˆåŠŸ')
    } catch (error) {
      ElMessage.error('åˆ›å»ºä»»åŠ¡å¤±è´¥')
    }
  }
  
  // å®Œæˆä»»åŠ¡
  const completeTask = async (id) => {
    try {
      const response = await taskApi.completeTask(id)
      
      // ä»ä»»åŠ¡åˆ—è¡¨ä¸­ç§»é™¤
      const task = tasks.value.find(t => t._id === id)
      if (task) {
        // å°†ä»»åŠ¡ç§»åŠ¨åˆ°å·²å®Œæˆåˆ—è¡¨
        completedTasks.value.unshift({
          ...task,
          completed: true,
          completedAt: new Date().toISOString()
        })
        // ä»å¾…åŠä»»åŠ¡ä¸­ç§»é™¤
        tasks.value = tasks.value.filter(t => t._id !== id)
      }
      
      // å¢åŠ æ¤ç‰©ç»éªŒå€¼
      const plantStore = usePlantStore()
      if (plantStore.plants && plantStore.plants.length > 0) {
        // è·å–ä¸»æ¤ç‰©
        const mainPlant = plantStore.plants.find(p => p.isMainPlant)
        if (mainPlant) {
          const plantId = mainPlant._id || mainPlant.id
          if (plantId) {
            // æ ¹æ®ä»»åŠ¡æ˜¯å¦é‡è¦ï¼Œå¢åŠ ä¸åŒçš„ç»éªŒå€¼
            // æ™®é€šä»»åŠ¡25ç‚¹ï¼Œé‡è¦ä»»åŠ¡50ç‚¹
            const expAmount = task && task.important ? 50 : 25;
            await plantStore.gainExperience(plantId, expAmount)
            
            // æ˜¾ç¤ºå…·ä½“è·å¾—çš„ç»éªŒå€¼
            ElMessage.success(`ä»»åŠ¡å·²å®Œæˆï¼Œè·å¾— ${expAmount} ç‚¹ç»éªŒå€¼ï¼`)
          }
        }
      } else {
        ElMessage.success('ä»»åŠ¡å·²å®Œæˆ')
      }
    } catch (error) {
      ElMessage.error('å®Œæˆä»»åŠ¡å¤±è´¥')
    }
  }
  
  // å®Œæˆç³»ç»Ÿä»»åŠ¡
  const completeSystemTask = async (id) => {
    try {
      const plantStore = usePlantStore()
      if (!plantStore.currentPlant) {
        ElMessage.warning('è¯·å…ˆæ·»åŠ ä¸€ä¸ªæ¤ç‰©')
        return false
      }
      
      const response = await taskApi.completeSystemTask(id)
      
      // æ›´æ–°ä»»åŠ¡çŠ¶æ€
      const index = systemTasks.value.findIndex(t => t._id === id)
      if (index !== -1) {
        systemTasks.value[index].completed = true
      }
      
      // å¦‚æœåç«¯å·²ç»å¤„ç†äº†ç»éªŒå€¼å¥–åŠ±ï¼Œåˆ™æ˜¾ç¤ºç›¸åº”çš„æç¤º
      if (response.rewards) {
        ElMessage.success(`å®Œæˆä»»åŠ¡è·å¾— ${response.rewards.experience} ç‚¹ç»éªŒ`)
      } 
      // å¦‚æœåç«¯æ²¡æœ‰å¤„ç†ç»éªŒå€¼å¥–åŠ±ï¼Œåˆ™åœ¨å‰ç«¯å¤„ç†
      else {
        // è·å–ä¸»æ¤ç‰©
        const plantId = plantStore.currentPlant._id || plantStore.currentPlant.id
        if (plantId) {
          // ç³»ç»Ÿä»»åŠ¡å›ºå®šå¢åŠ 35ç‚¹ç»éªŒå€¼
          const expAmount = 35;
          await plantStore.gainExperience(plantId, expAmount)
          ElMessage.success(`å®Œæˆç³»ç»Ÿä»»åŠ¡è·å¾— ${expAmount} ç‚¹ç»éªŒå€¼`)
        }
      }
      
      return true
    } catch (error) {
      if (error.response?.status === 400) {
        ElMessage.warning('ä»Šå¤©å·²ç»å®Œæˆè¿‡æ­¤ä»»åŠ¡')
      } else {
        ElMessage.error('å®Œæˆç³»ç»Ÿä»»åŠ¡å¤±è´¥')
      }
      return false
    }
  }
  
  // ç§»é™¤ä»»åŠ¡
  const removeTask = async (id) => {
    try {
      // æ£€æŸ¥IDæ˜¯å¦æœ‰æ•ˆ
      if (!id) {
        ElMessage.error('ä»»åŠ¡IDæ— æ•ˆï¼Œæ— æ³•åˆ é™¤')
        return
      }
      
      await taskApi.deleteTask(id)
      tasks.value = tasks.value.filter(task => task._id !== id)
      ElMessage.success('ä»»åŠ¡å·²åˆ é™¤')
    } catch (error) {
      console.error('åˆ é™¤ä»»åŠ¡å¤±è´¥:', error)
      ElMessage.error('åˆ é™¤ä»»åŠ¡å¤±è´¥')
    }
  }
  
  // åˆ é™¤å·²å®Œæˆä»»åŠ¡
  const removeCompletedTask = async (id) => {
    try {
      // æ£€æŸ¥IDæ˜¯å¦æœ‰æ•ˆ
      if (!id) {
        ElMessage.error('ä»»åŠ¡IDæ— æ•ˆï¼Œæ— æ³•åˆ é™¤')
        return
      }
      
      await taskApi.deleteTask(id)
      completedTasks.value = completedTasks.value.filter(task => task._id !== id)
      ElMessage.success('ä»»åŠ¡å·²åˆ é™¤')
    } catch (error) {
      console.error('åˆ é™¤ä»»åŠ¡å¤±è´¥:', error)
      ElMessage.error('åˆ é™¤ä»»åŠ¡å¤±è´¥')
    }
  }
  
  // æ›´æ–°ä»»åŠ¡
  const updateTask = async (updatedTask) => {
    try {
      const { _id, title, description, deadline, important } = updatedTask
      
      const response = await taskApi.updateTask(_id, {
        title,
        description,
        deadline,
        important
      })
      
      // æ›´æ–°æœ¬åœ°çŠ¶æ€
      const index = tasks.value.findIndex(t => t._id === _id)
      if (index !== -1) {
        tasks.value[index] = response.task
      }
      
      ElMessage.success('ä»»åŠ¡å·²æ›´æ–°')
    } catch (error) {
      ElMessage.error('æ›´æ–°ä»»åŠ¡å¤±è´¥')
    }
  }
  
  // åˆ‡æ¢ä»»åŠ¡é‡è¦æ€§
  const toggleTaskImportance = async (id) => {
    const task = tasks.value.find(t => t._id === id)
    if (task) {
      await updateTask({
        ...task,
        important: !task.important
      })
    }
  }

  return {
    tasks,
    systemTasks,
    completedTasks,
    pendingTasks,
    loading,
    
    fetchTasks,
    fetchSystemTasks,
    addTask,
    completeTask,
    completeSystemTask,
    removeTask,
    removeCompletedTask,
    updateTask,
    toggleTaskImportance
  }
}) 